import React from 'react';
import {
    Box,
    Grid,
    TextField,
    Typography,
    Paper,
    Divider,
    Tooltip,
    IconButton,
} from '@mui/material';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import { useCharacterContext } from '../context';
import { CharacterOrigin } from '../../../types/character';

interface FieldConfig {
    name: string;
    label: string;
    help: string;
    type: string;
    multiline?: boolean;
}

interface FieldGroup {
    title: string;
    description: string;
    fields: FieldConfig[];
}

const Origin: React.FC = () => {
    const { character, updateCharacter } = useCharacterContext();

    const handleChange = (field: string, value: string | number) => {
        const origin = { ...character.origin } as CharacterOrigin;
        
        const fields = field.split('.');
        let current: any = origin;
        for (let i = 0; i < fields.length - 1; i++) {
            if (!current[fields[i]]) {
                current[fields[i]] = {};
            }
            current = current[fields[i]];
        }
        current[fields[fields.length - 1]] = value;

        updateCharacter({ origin });
    };

    const fieldGroups: FieldGroup[] = [
        {
            title: "",
            description: "",
            fields: [
                {
                    name: "playerName",
                    label: "",
                    help: "",
                    type: "text"
                },
                {
                    name: "characterName",
                    label: "",
                    help: "",
                    type: "text"
                },
                {
                    name: "gender",
                    label: "",
                    help: "DND",
                    type: "text"
                },
                {
                    name: "age",
                    label: "",
                    help: "700100",
                    type: "number"
                },
                {
                    name: "height",
                    label: "",
                    help: "180cm",
                    type: "text"
                },
                {
                    name: "weight",
                    label: "",
                    help: "75kg",
                    type: "text"
                }
            ]
        },
        {
            title: "",
            description: "",
            fields: [
                {
                    name: "alignment",
                    label: "",
                    help: "",
                    type: "text"
                },
                {
                    name: "faith",
                    label: "",
                    help: "",
                    type: "text"
                }
            ]
        },
        {
            title: "",
            description: "",
            fields: [
                {
                    name: "appearance.hairStyle",
                    label: "",
                    help: "",
                    type: "text"
                },
                {
                    name: "appearance.hairColor",
                    label: "",
                    help: "",
                    type: "text"
                },
                {
                    name: "appearance.eyeColor",
                    label: "",
                    help: "",
                    type: "text"
                },
                {
                    name: "appearance.skinColor",
                    label: "",
                    help: "",
                    type: "text"
                }
            ]
        },
        {
            title: "",
            description: "",
            fields: [
                {
                    name: "personality.traits",
                    label: "",
                    help: "",
                    type: "text",
                    multiline: true
                },
                {
                    name: "personality.ideals",
                    label: "",
                    help: "",
                    type: "text",
                    multiline: true
                },
                {
                    name: "personality.bonds",
                    label: "",
                    help: "",
                    type: "text",
                    multiline: true
                },
                {
                    name: "personality.flaws",
                    label: "",
                    help: "",
                    type: "text",
                    multiline: true
                }
            ]
        },
        {
            title: "",
            description: "",
            fields: [
                {
                    name: "homeland",
                    label: "",
                    help: "",
                    type: "text",
                    multiline: true
                },
                {
                    name: "description",
                    label: "",
                    help: "",
                    type: "text",
                    multiline: true
                },
                {
                    name: "backstory",
                    label: "",
                    help: "",
                    type: "text",
                    multiline: true
                }
            ]
        }
    ];

    return (
        <Box sx={{ p: 3 }}>
            {fieldGroups.map((group, index) => (
                <Paper key={index} sx={{ p: 3, mb: 3 }}>
                    <Box sx={{ mb: 2 }}>
                        <Typography variant="h5" gutterBottom>
                            {group.title}
                        </Typography>
                        <Typography variant="body2" color="text.secondary" paragraph>
                            {group.description}
                        </Typography>
                    </Box>
                    <Grid container spacing={3}>
                        {group.fields.map((field, fieldIndex) => (
                            <Grid item xs={12} md={field.multiline ? 12 : 6} key={fieldIndex}>
                                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                    <TextField
                                        fullWidth
                                        label={field.label}
                                        type={field.type}
                                        value={
                                            field.name.includes('.')
                                                ? field.name.split('.').reduce((obj: any, key) => obj?.[key] ?? '', character.origin || {})
                                                : (character.origin as any)?.[field.name] ?? ''
                                        }
                                        onChange={(e) => handleChange(
                                            field.name,
                                            field.type === 'number' ? Number(e.target.value) : e.target.value
                                        )}
                                        multiline={field.multiline}
                                        rows={field.multiline ? 4 : 1}
                                        sx={{ mr: 1 }}
                                    />
                                    <Tooltip title={field.help} placement="right">
                                        <IconButton size="small">
                                            <HelpOutlineIcon />
                                        </IconButton>
                                    </Tooltip>
                                </Box>
                            </Grid>
                        ))}
                    </Grid>
                </Paper>
            ))}
        </Box>
    );
};

export default Origin;