import React, { useMemo, useState, useRef } from 'react';
import { 
  Box, 
  Typography, 
  Divider, 
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
  Tooltip,
  Fade,
  Snackbar,
  Alert,
  Menu,
  MenuItem
} from '@mui/material';
import RestartAltIcon from '@mui/icons-material/RestartAlt';
import FileUploadIcon from '@mui/icons-material/FileUpload';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import { useCharacterContext } from '../context';
import { AbilityName, Feature } from '../../../types/character';
import { AbilityScoreIncrease, RacialTrait } from '../../../types/race';
import { ClassFeature } from '../../../types/Class';
import { LevelingService } from '../../../services/LevelingService';
import { GameDataService } from '../../../services/GameDataService';
import { LocalStorageService } from '../../../services/LocalStorageService';
import { CharacterExportService } from '../../../services/CharacterExportService';

interface StatSectionProps {
  title: string;
  children: React.ReactNode;
}

const StatSection: React.FC<StatSectionProps> = ({ title, children }) => (
  <Box>
    <Typography variant="h6" gutterBottom>
      {title}
    </Typography>
    {children}
  </Box>
);

const translateAbility = (ability: string): string => {
  switch (ability) {
    case 'strength': return '';
    case 'dexterity': return '';
    case 'constitution': return '';
    case 'intelligence': return '';
    case 'wisdom': return '';
    case 'charisma': return '';
    default: return ability;
  }
};

const Stats: React.FC = () => {
  const { character, resetCharacter, updateCharacter } = useCharacterContext();
  const [resetDialogOpen, setResetDialogOpen] = useState(false);
  const [snackbar, setSnackbar] = useState<{
    open: boolean;
    message: string;
    severity: 'success' | 'error';
  }>({
    open: false,
    message: '',
    severity: 'success'
  });
  const fileInputRef = useRef<HTMLInputElement>(null);
  const levelingService = LevelingService.getInstance();
  const gameDataService = GameDataService.getInstance();
  const exportService = CharacterExportService.getInstance();

  const [exportAnchorEl, setExportAnchorEl] = useState<null | HTMLElement>(null);

  const getAbilityModifier = (score: number): string => {
    const modifier = Math.floor((score - 10) / 2);
    return modifier >= 0 ? `+${modifier}` : modifier.toString();
  };

  const renderRacialAbilityScores = () => {
    if (!character.race?.abilityScoreIncrease) return null;

    const increases = Object.entries(character.race.abilityScoreIncrease)
      .filter(([_, value]) => typeof value === 'number')
      .map(([ability, value]) => `${translateAbility(ability)} +${value}`);

    if (increases.length === 0) return null;

    return (
      <Typography>
        {increases.join('')}
      </Typography>
    );
  };

  const renderSubraceAbilityScores = () => {
    if (!character.subrace?.abilityScoreIncrease) return null;

    const increases = Object.entries(character.subrace.abilityScoreIncrease)
      .filter(([_, value]) => typeof value === 'number')
      .map(([ability, value]) => `${translateAbility(ability)} +${value}`);

    if (increases.length === 0) return null;

    return (
      <Typography>
        {increases.join('')}
      </Typography>
    );
  };

  const classFeatures = useMemo(() => {
    if (!character.classes?.[0]) return [];
    const classData = gameDataService.getClass(character.classes[0].name);
    if (!classData) return [];
    return classData.features.filter(feature => 
      feature.level <= character.classes[0].level
    );
  }, [character.classes]);

  const subclassFeatures = useMemo(() => {
    if (!character.classes?.[0]?.subclass) return [];
    const classData = gameDataService.getClass(character.classes[0].name);
    if (!classData) return [];
    const subclass = classData.subclasses.find(sc => sc.name === character.classes[0].subclass);
    if (!subclass) return [];
    return subclass.features.filter(feature => 
      feature.level <= character.classes[0].level
    );
  }, [character.classes]);

  const handleResetClick = () => {
    setResetDialogOpen(true);
  };

  const handleResetConfirm = () => {
    resetCharacter();
    setResetDialogOpen(false);
  };

  const handleResetCancel = () => {
    setResetDialogOpen(false);
  };

  const handleExportClick = (event: React.MouseEvent<HTMLElement>) => {
    setExportAnchorEl(event.currentTarget);
  };

  const handleExportClose = () => {
    setExportAnchorEl(null);
  };

  const handleExportFormat = async (format: 'json' | 'markdown') => {
    try {
      await exportService.exportCharacter(character, format);
      setSnackbar({
        open: true,
        message: '',
        severity: 'success'
      });
    } catch (error) {
      setSnackbar({
        open: true,
        message: '',
        severity: 'error'
      });
    }
    handleExportClose();
  };

  const handleImportClick = () => {
    fileInputRef.current?.click();
  };

  const handleFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    try {
      const importedCharacter = await exportService.importCharacter(file);
      updateCharacter(importedCharacter);
      setSnackbar({
        open: true,
        message: '',
        severity: 'success'
      });
    } catch (error) {
      setSnackbar({
        open: true,
        message: '',
        severity: 'error'
      });
    }

    // 
    event.target.value = '';
  };

  const handleSnackbarClose = () => {
    setSnackbar(prev => ({ ...prev, open: false }));
  };

  return (
    <Box sx={{ position: 'relative', height: '100%' }}>
      {/*  */}
      <StatSection title="">
        <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: 1 }}>
          <Typography>
            {character.finalAbilityScores.strength} ({getAbilityModifier(character.finalAbilityScores.strength)})
          </Typography>
          <Typography>
            {character.finalAbilityScores.dexterity} ({getAbilityModifier(character.finalAbilityScores.dexterity)})
          </Typography>
          <Typography>
            {character.finalAbilityScores.constitution} ({getAbilityModifier(character.finalAbilityScores.constitution)})
          </Typography>
          <Typography>
            {character.finalAbilityScores.intelligence} ({getAbilityModifier(character.finalAbilityScores.intelligence)})
          </Typography>
          <Typography>
            {character.finalAbilityScores.wisdom} ({getAbilityModifier(character.finalAbilityScores.wisdom)})
          </Typography>
          <Typography>
            {character.finalAbilityScores.charisma} ({getAbilityModifier(character.finalAbilityScores.charisma)})
          </Typography>
        </Box>
        {renderRacialAbilityScores()}
        {renderSubraceAbilityScores()}
      </StatSection>

      <Divider sx={{ my: 2 }} />

      <StatSection title="">
        <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: 1 }}>
          <Typography>
            {character.race?.displayName}
          </Typography>
          <Typography>
            {character.subrace?.displayName || ''}
          </Typography>
          <Typography>
            {character.classes?.[0]?.displayName || ''}
          </Typography>
          <Typography>
            {character.background?.name || ''}
          </Typography>
        </Box>
      </StatSection>

      <Divider sx={{ my: 2 }} />

      <StatSection title="">
        <Box sx={{ display: 'grid', gridTemplateColumns: '1fr', gap: 1 }}>
          {character.race?.racialTraits && character.race.racialTraits.length > 0 && (
            <Typography>
              {character.race.racialTraits.map(trait => trait.name).join('')}
            </Typography>
          )}
          {character.subrace?.traits && character.subrace.traits.length > 0 && (
            <Typography>
              {character.subrace.traits.map(trait => trait.name).join('')}
            </Typography>
          )}
          {classFeatures.length > 0 && (
            <Typography>
              {classFeatures.map(feature => feature.name).join('')}
            </Typography>
          )}
          {subclassFeatures.length > 0 && (
            <Typography>
              {subclassFeatures.map(feature => feature.name).join('')}
            </Typography>
          )}
          {character.background?.features && character.background.features.length > 0 && (
            <Typography>
              {character.background.features.map(feature => feature.name).join('')}
            </Typography>
          )}
        </Box>
      </StatSection>

      <Divider sx={{ my: 2 }} />

      <StatSection title="">
        <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: 1 }}>
          <Typography>
            {character.hitPoints || 0}
          </Typography>
          <Typography>
            {character.armorClass || 0}
          </Typography>
          <Typography>
            {character.initiative || 0}
          </Typography>
          <Typography>
            {character.speed || 30}
          </Typography>
        </Box>
      </StatSection>

      {/*  */}
      <Box
        sx={{
          position: 'absolute',
          bottom: 16,
          right: 16,
          display: 'flex',
          gap: 1,
        }}
      >
        <IconButton
          onClick={handleExportClick}
          size="small"
          sx={{
            bgcolor: 'background.paper',
            boxShadow: 1,
            '&:hover': { bgcolor: 'background.default' },
            color: 'primary.main'
          }}
        >
          <FileDownloadIcon />
        </IconButton>
        <Menu
          anchorEl={exportAnchorEl}
          open={Boolean(exportAnchorEl)}
          onClose={handleExportClose}
        >
          <MenuItem onClick={() => handleExportFormat('json')}>
             JSON
          </MenuItem>
          <MenuItem onClick={() => handleExportFormat('markdown')}>
             Markdown
          </MenuItem>
        </Menu>
        <IconButton
          onClick={handleImportClick}
          size="small"
          sx={{
            bgcolor: 'background.paper',
            boxShadow: 1,
            '&:hover': { bgcolor: 'background.default' },
            color: 'primary.main'
          }}
        >
          <FileUploadIcon />
        </IconButton>
        <input
          type="file"
          ref={fileInputRef}
          onChange={handleFileChange}
          style={{ display: 'none' }}
          accept=".json,.md"
        />
        <Tooltip 
          title="" 
          placement="top" 
          TransitionComponent={Fade}
          TransitionProps={{ timeout: 600 }}
        >
          <IconButton
            onClick={handleResetClick}
            color="primary"
            sx={{
              backgroundColor: 'background.paper',
              boxShadow: 2,
              '&:hover': {
                backgroundColor: 'background.default',
              },
            }}
          >
            <RestartAltIcon />
          </IconButton>
        </Tooltip>
      </Box>

      {/*  */}
      <Dialog
        open={resetDialogOpen}
        onClose={handleResetCancel}
        PaperProps={{
          sx: {
            borderRadius: 2,
            boxShadow: 24,
          }
        }}
      >
        <DialogTitle></DialogTitle>
        <DialogContent>
          <DialogContentText>
            
          </DialogContentText>
        </DialogContent>
        <DialogActions sx={{ padding: 2 }}>
          <Button 
            onClick={handleResetCancel}
            sx={{ 
              minWidth: 100,
              borderRadius: 2,
            }}
          >
            
          </Button>
          <Button 
            onClick={handleResetConfirm}
            variant="contained"
            color="error"
            sx={{ 
              minWidth: 100,
              borderRadius: 2,
            }}
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={3000}
        onClose={handleSnackbarClose}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'left' }}
      >
        <Alert
          onClose={handleSnackbarClose}
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Stats;
